import React, { useState, useEffect, Suspense } from 'react'

// ** Router Import
import Router from './router/Router'

// ** Routes & Default Routes
import { getRoutes } from './router/routes'

// ** Hooks Imports
import { useLayout } from '@hooks/useLayout'
import { Alert, Button } from 'reactstrap'
import { AlertCircle } from 'react-feather'

const App = () => {

  const [allRoutes, setAllRoutes] = useState([])
  const [refeshApp, setefreshApp] = useState()
  const [updateExists, setUpdateExists] = useState(false)

  // ** Hooks
  const { layout } = useLayout()

  useEffect(() => {
    setAllRoutes(getRoutes(layout))
  }, [layout])

  const refreshApp = () => {
    console.log("skipWaiting started")
    setUpdateExists(false)
    if (!refeshApp) {
      console.warn("No worker data found when trying to refresh!")
      return
    }
    refeshApp.postMessage({ type: "SKIP_WAITING" })
  }

  const showRefreshUI = (e) => {
    if (e.detail) {
      setefreshApp(e.detail)
      setUpdateExists(true)
      console.log("Showing refresh button.")
    } else {
      console.warn("No worker data found!")
    }
  }

  useEffect(() => {
    document.addEventListener("swUpdated", showRefreshUI, { once: true })
    if (navigator.serviceWorker) {
      navigator.serviceWorker.addEventListener(
        //triggered by registration.claim
        "controllerchange",
        () => {
          if (updateExists) return
          setUpdateExists(true)
          console.log("controllerchange triggered, -> auto refresh!!")
          window.location.reload()
        }
      )
    }
  }, [])

  return (
    <div>
      {updateExists &&
        <div className='app-refresh-box'>
          <Alert color='bg-waring' isOpen={updateExists} className='bg-warning'>
            <div className='alert-body'>
              <AlertCircle size={15} />{' '}
              New version Available, Click Refresh {'  '}
              <Button size='sm' color='primary' onClick={() => { refreshApp() }}>Refresh</Button>
            </div>
          </Alert>
        </div>
      }
      <Suspense fallback={null}>
        <Router allRoutes={allRoutes} />
      </Suspense>
    </div>
  )
}

export default App
